'use client'

import { useState } from 'react'
import { useData, Request } from '@/lib/data-context'
import { AuthLayout } from '@/components/auth-layout'
import { RequestForm } from '@/components/requests/request-form'
import { RequestList } from '@/components/requests/request-list'
import { Button } from '@/components/ui/button'
import { Card } from '@/components/ui/card'
import { useConfirmDialog } from '@/hooks/use-confirm-dialog'
import { ConfirmDialog } from '@/components/confirm-dialog'
import { InfoDialog } from '@/components/info-dialog'
import { useToast } from '@/hooks/use-toast'
import { PageHeader } from '@/components/page-header'

export default function RequestsPage() {
  const { requests, addRequest, updateRequest, deleteRequest, institutions, materials } = useData()
  const [isFormVisible, setIsFormVisible] = useState(false)
  const [editingRequest, setEditingRequest] = useState<Request | null>(null)
  const [infoDialog, setInfoDialog] = useState({ isOpen: false, title: '', description: '', variant: 'info' as 'info' | 'warning' | 'success' })
  const { toast } = useToast()
  const confirmDialog = useConfirmDialog()

  const pendingCount = requests.filter(r => r.status === 'pending').length
  const deliveredCount = requests.filter(r => r.status === 'delivered').length
  const totalValue = requests.reduce((acc, r) => acc + r.totalValue, 0)

  const handleAddNew = () => {
    if (institutions.length === 0 || materials.length === 0) {
      const missing = []
      if (institutions.length === 0) missing.push('instituições')
      if (materials.length === 0) missing.push('materiais')
      
      setInfoDialog({
        isOpen: true,
        title: 'Dados Necessários',
        description: `Você precisa cadastrar ${missing.join(' e ')} antes de criar uma requisição. Acesse as páginas correspondentes para cadastrar.`,
        variant: 'warning',
      })
      return
    }
    setEditingRequest(null)
    setIsFormVisible(true)
  }

  const handleEdit = (request: Request) => {
    setEditingRequest(request)
    setIsFormVisible(true)
  }

  const handleSubmit = (data: Omit<Request, 'id' | 'requestNumber' | 'createdAt'>) => {
    if (editingRequest) {
      updateRequest(editingRequest.id, data)
      toast({
        title: 'Requisição atualizada',
        description: 'A requisição foi atualizada com sucesso.',
      })
    } else {
      addRequest(data)
      toast({
        title: 'Requisição criada',
        description: 'A requisição foi criada com sucesso.',
      })
    }
    setIsFormVisible(false)
    setEditingRequest(null)
  }

  const handleCancel = () => {
    setIsFormVisible(false)
    setEditingRequest(null)
  }

  const handleDelete = async (id: string) => {
    const request = requests.find(r => r.id === id)
    const confirmed = await confirmDialog.confirm({
      title: 'Excluir Requisição',
      description: `Tem certeza que deseja excluir a requisição "${request?.requestNumber}"? Esta ação não pode ser desfeita.`,
      confirmText: 'Excluir',
      cancelText: 'Cancelar',
      variant: 'destructive',
    })

    if (confirmed) {
      deleteRequest(id)
      toast({
        title: 'Requisição excluída',
        description: 'A requisição foi excluída com sucesso.',
        variant: 'destructive',
      })
    }
  }

  return (
    <AuthLayout>
      <div className="p-6 lg:p-8">
        <PageHeader
          title="Requisições de Materiais"
          description="Gerencie as requisições de saída de materiais das instituições"
        />

        {!isFormVisible && (
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <Card className="p-5 border border-yellow-500/20 bg-yellow-500/5 hover:border-yellow-500/40 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Requisições Pendentes</div>
              <div className="text-2xl font-semibold text-yellow-600">{pendingCount}</div>
            </Card>
            <Card className="p-5 border border-border/50 hover:border-secondary/50 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Já Entregues</div>
              <div className="text-2xl font-semibold text-foreground">{deliveredCount}</div>
            </Card>
            <Card className="p-5 border border-border/50 hover:border-accent/50 transition-colors">
              <div className="text-xs font-medium text-muted-foreground uppercase tracking-wide mb-3">Valor Total em Requisições</div>
              <div className="text-2xl font-semibold text-foreground">R$ {totalValue.toFixed(2)}</div>
            </Card>
          </div>
        )}

        <div className="mb-6">
          <Button onClick={handleAddNew} className="bg-primary hover:bg-primary/90 font-medium">
            Nova Requisição
          </Button>
        </div>
        <RequestList
          requests={requests}
          onEdit={handleEdit}
          onDelete={handleDelete}
        />

        <RequestForm
          request={editingRequest || undefined}
          isOpen={isFormVisible}
          onSubmit={handleSubmit}
          onCancel={handleCancel}
        />
      </div>

      <ConfirmDialog
        isOpen={confirmDialog.isOpen}
        title={confirmDialog.options.title}
        description={confirmDialog.options.description}
        confirmText={confirmDialog.options.confirmText}
        cancelText={confirmDialog.options.cancelText}
        variant={confirmDialog.options.variant}
        onConfirm={confirmDialog.handleConfirm}
        onCancel={confirmDialog.handleCancel}
      />

      <InfoDialog
        isOpen={infoDialog.isOpen}
        title={infoDialog.title}
        description={infoDialog.description}
        variant={infoDialog.variant}
        onClose={() => setInfoDialog({ ...infoDialog, isOpen: false })}
      />
    </AuthLayout>
  )
}
